Home.propTypes = {
  isMobile: PropTypes.bool,
  dispatchDeviceType: PropTypes.func
}

const mapStateToProps = createPropsSelector({
  isMobile: isMobile
})

const mapDispatchToProps = {
  dispatchDeviceType
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Home);
